import { useState } from "react"
import { useDispatch } from "react-redux"
import { useSelector } from "react-redux"
import { ADD_QUANTITY, REMOVE_QUANTITY, REMOVE_TO_CART } from "../store/action/action"

function Cart(){

    const dataPushInRedux = useDispatch()

    const cartProductFromRedux = useSelector((state)=>state.cart)

    function addQuantity(id){
        dataPushInRedux(ADD_QUANTITY(id))
    }

    function removeQuantity(id){
        dataPushInRedux(REMOVE_QUANTITY(id))
    }

    return(
        <div>
            <div className='cartBox'>

                {cartProductFromRedux.map((data)=>{

                    return (
                        <div className="cartItem" key={Math.random()}>
                            <img src={data.image}/>
                            <div className="proInfo">
                                <h3>{data.title}</h3>
                                <p>{data.description}</p>
                            </div>
                            <div className="itemQuantity">
                                <button onClick={()=>addQuantity(data.id)}>+</button>
                                <p>{data.quantity}</p>
                                <button onClick={()=>removeQuantity(data.id)}>-</button>
                            </div>

                            <div className="price">$ {data.price.toFixed(2)}</div>

                            <button className='removebtn' onClick={()=>dataPushInRedux(REMOVE_TO_CART(data.id))}>Remove</button>
                        </div>
                    )

                })}
                
            </div>
        </div>
    )
}

export default Cart