
export const PRODUCT = (data)=>{
   return {
    type: "PRODUCT",
    data
   }
}

export function ADD_CART(data){
    return {
        type: "ADD_CART",
        data
    }
}

export function ADD_QUANTITY(data) { 
    return{
        type: "ADD_QUANTITY",
        data
    }
}

export function REMOVE_QUANTITY(data) { 
    return{
        type: "REMOVE_QUANTITY",
        data
    }
}

export function REMOVE_TO_CART(data){
    return {
        type: "REMOVE_TO_CART",
        data
    }
}