const initialState = []

export default function addCart( state = initialState , action){
    switch (action.type) {
        case "ADD_CART":
            return state = state.concat(action.data)
            break;

        case "ADD_QUANTITY":

            const oldCart = state

            const findCartItemById = state.filter((data)=>{
                return data.id == action.data 

            })
            
            const oldQuanttity = findCartItemById[0].quantity

            findCartItemById[0].quantity +=1

            const newQuantity = findCartItemById[0].quantity

            findCartItemById[0].price = (findCartItemById[0].price / oldQuanttity)*newQuantity

            const removeProductNewCart = oldCart.filter((data)=>{
                return data.id != action.id
            })


            return removeProductNewCart
            break;

        case "REMOVE_QUANTITY":

            const oldCart2 = state
            
            const findCartItemByIdforRemove = state.filter((data)=>{
                return data.id == action.data
            })
            
            const quantity = findCartItemByIdforRemove[0].quantity

            if (quantity>1) {

                const oldQuanttity = findCartItemByIdforRemove[0].quantity

                findCartItemByIdforRemove[0].quantity -=1

                const newQuantity = findCartItemByIdforRemove[0].quantity

                findCartItemByIdforRemove[0].price = (findCartItemByIdforRemove[0].price / oldQuanttity)*newQuantity

                const removeProductNewCart2 = oldCart2.filter((data)=>{
                    return data.id != action.id
                })

                state = removeProductNewCart2
            

            } else {
                alert("You have only one product in cart")
            }


            return state

        case "REMOVE_TO_CART":
            const stateData = state.filter((data)=>{
                return data.id != action.data
            })

            return stateData
        break;

        default:
            return state
            break;
    }
}