import { useEffect } from "react"
import { useSelector } from "react-redux"
import { useDispatch } from "react-redux"
import { NavLink } from "react-router-dom"
import { ADD_CART, PRODUCT } from "../store/action/action"
import data from "../fakeData/data"

function Home(){

    const dataPushInRedux = useDispatch()

    const productFromRedux = useSelector((state)=>state.product)
    const cartFromRedux = useSelector((state)=>state.cart)

    useEffect(()=>{
        // fetch('https://fakestoreapi.com/products')
        // .then((data)=>data.json())
        // .then((data)=>dataPushInRedux(PRODUCT(data)))
        // .catch((e)=>console.log(e))

        dataPushInRedux(PRODUCT(data))
        
    },[])

    function addToCart(id){

        const checkProductOnCart = cartFromRedux.filter((data)=>{
            return data.id == id
        })

        const checkResult = checkProductOnCart.length

        if (checkResult>0) {
            alert("Product already in cart")

        }else{
            const cartProduct = productFromRedux.filter((data)=>{
                return data.id == id
            })
            
            //menualy quantity cart
            cartProduct[0].quantity = 1

            dataPushInRedux(ADD_CART(cartProduct[0]))
        }
    }


    return(
        <div>
            <NavLink to="/cart" className="cartButton">{cartFromRedux.length} Product In Cart</NavLink>
            <div className="productBox">

                {productFromRedux.map((data)=>{
                    return(
                    <div className="card" key={Math.random()}>
                        <img src={data.image} />
                        <h1>{data.title}</h1>
                        <p className="price">{data.price}</p>
                        <p>{data.description}</p>
                        <p><button onClick={()=>addToCart(data.id)} >Add to Cart</button></p>
                    </div>
                    )
                })}
                
            </div> 
        </div>
    )
}

export default Home